import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'threeshoptitle'
})
export class ThreeshoptitlePipe implements PipeTransform {

  transform(value: string, args?: any): any {
    if (!value || value.length < 10) {
      return value;
    }
    let name: string;
    name = value.substr(0, 10) + '...'
    return name;
  }

}
