import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {ActionSheetController} from "@ionic/angular";

@Component({
  selector: 'app-hwcpsort',
  templateUrl: './hwcpsort.component.html',
  styleUrls: ['./hwcpsort.component.scss'],
})
export class HwcpsortComponent implements OnInit {
  months: any[]; // 
  mindate: string;
  nowYear: string;
  nowfulldate: string;
  startDate: string; // 
  endData: string; // 
  setShow: boolean// 
  pricearea: any[]; // 
  settitle: string; // 
  ishwactive: number; // 
  ishwclick: number; // 
  allAddress: object;
  @Input() togglelist: any[]; // 
  @Output() sethwActivesortfn = new EventEmitter();

  constructor(public actionSheetController: ActionSheetController) {
    this.ishwactive = 1;

    const myDate = new Date();//
    const nowMonth = myDate.getMonth() + 1;
    let nextMonth;
    if (nowMonth == 12) {
      nextMonth = 1;
    } else {
      nextMonth = nowMonth + 1;
    }
    let endMonth;
    if (nextMonth == 12) {
      endMonth = 1;
    } else {
      endMonth = nextMonth + 1;
    }
    this.months = [
      {
        name: nowMonth + '',
        id: 1,
        active: true
      },
      {
        name: nextMonth + '',
        id: 2,
        active: false
      },
      {
        name: endMonth + '',
        id: 3,
        active: false
      }
    ]
    this.nowYear = myDate.getFullYear().toString();
    const nowdate = myDate.getDate();
    const nowfullmonth = nowMonth < 10 ? '0' + nowMonth : nowMonth;
    this.nowfulldate = nowdate < 10 ? '0' + nowdate : nowdate.toString();
    this.mindate = this.nowYear + '-' + nowfullmonth + '-' + this.nowfulldate;
    this.startDate = this.nowYear + '-' + nowfullmonth + '-' + this.nowfulldate;
    this.pricearea = [{
      name: '',
      id: 1,
      active: true
    }, {
      name: '200',
      id: 2,
      active: false
    }]
    this.allAddress = {
      name: '',
      id: 1,
      active: true
    }
  }

  ngOnInit() {}
  async showzhpx() {
    if (this.ishwactive !== 1) {
      this.ishwactive = 1;
      const item = this.togglelist[0];
      this.sethwActivesortfn.emit(item);
      return false;
    }
    let arr = [];
    for (let i = 0, j = this.togglelist.length; i < j; i++) {
      const item = this.togglelist[i];
      const obj: object = {
        text: item.name,
        name: item.id,
        handler: () => {
          this.ishwactive = 1;
          // this.sethwActivesort(item);
          this.sethwActivesortfn.emit(item);
        }
      }
      arr.push(obj);
    }
    const obj = {
      text: '',
      role: 'cancel',
      handler: () => {
        console.log('Cancel clicked');
      }
    };
    arr.push(obj)
    const actionSheet = await this.actionSheetController.create({
      mode: 'md',
      buttons: arr
    });
    await actionSheet.present();
  }

  showoter (type: number) {
    // this.ishwactive = type;
    this.ishwclick = type;
    this.setShow = true;
    if (type === 4) {
      this.settitle = ''
    } else if (type === 3) {
      this.settitle = ''
    } else if (type === 2) {
      this.settitle = ''
    }
  }
  setMonths ($event) {
    console.log($event)
    this.months.map(res => {
      if (res.id === $event.id) {
        res.active = true;
        let smonth = res.name.substr(0, 1)
        const month = smonth < 10 ? '0' + smonth : smonth;
        if (res.id === 1) {
          this.mindate = this.nowYear + '-' + month + '-' + this.nowfulldate;
        } else {
          this.mindate = this.nowYear + '-' + month + '-01';
        }
        this.startDate = this.mindate;
        this.endData = ''
        console.log(this.mindate)
      } else {
        res.active = false;
      }
    })
  }
  changestart () {
    this.endData = ''
  }
  changeend() { // 
    if (this.endData) {
      this.setShow = false;
      this.ishwactive = 4;
    }

  }
  setPricearea ($event) {
    console.log($event)
    this.pricearea.map(res => {
      if (res.id === $event.id) {
        res.active = true;
        this.ishwactive = 3;
      } else {
        res.active = false;
      }
    })
    this.setShow = false;
  }
  setAddress ($event) {
    // : todo 
  }
}
